@charset "UTF-8";
@font-face {
  font-family: USARugby;
  src: url("/wp-content/themes/rugbydb/dist/fonts/AGLettericaCondL-BoldItalic.ttf") format("truetype"); }

#page {
  min-height: 100vh; }
  @media screen and (min-width: 48.9375em) {
    #page #primary {
      margin-top: 75px; }
      .tax-wpcm_team #page #primary {
        margin-top: 0;
        padding-top: 0; } }
  .ua-mobile #page #primary,
  .ua-tablet #page #primary {
    margin-top: 46px; }

article .entry-title, article .entry-content {
  margin: 0; }

article .entry-title {
  padding-left: 0.5em; }

article .entry-content {
  padding: 0 0.5em; }
  @media screen and (min-width: 40em) {
    article .entry-content {
      padding: 0 0.9375em; } }

article h3 {
  margin: 0; }
  article h3 + p {
    margin-top: 0; }
    article h3 + p.address {
      margin-bottom: 0; }

