  background-size: cover;
  background-position: 50%;
  background-repeat: no-repeat;
  position: relative;
  display: inline-block;
  height: 0.75em;
  width: 1em;
  line-height: 1;
  padding: 0.15em;
  box-shadow: 0 0 1px; }
